body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.header {
  display: flex;
  margin-top: 15px;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.logo {
  height: 70px;
  width: 70px;
  border-radius: 50%;
}
.menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.menu > li {
  list-style: none;
  margin: 0 50px 0 50px;
  text-transform: uppercase;
}
.team-body {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin: 0px 50px 50px 50px;
}
.headings {
  background: linear-gradient(360deg, #234eb0 5%, #9010b0 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
h1 {
  font-weight: 800;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 80px;
  line-height: 45px;
  letter-spacing: 2%;
  padding-top: 1.5rem !important;
  padding-bottom: 1.5rem !important;
}
.small-txt{
  margin-top: -30px;
  font-size: 1.8rem;
  font-weight: 600;
}
.inst{
  display: flex ;
  width: 100%;
  margin-top: 50px;
  flex-wrap: wrap;
  justify-content: space-between;
}
.univ{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 30px 20px 30px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 5px;
  font-size: 20px;
  font-weight: 500;
  transition: 0.5s;
  width: 281.737px;
}
.univ>img{
  height: 70px;
  width: auto;
  padding-right: 5px;
}
.univ:hover{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: 0.5s;
}
.Mentor{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin: 100px 50px 30px 50px;
}
h2{
  font-size: 34px;
  color: #3F3FB0;
}
.container-profile{
  display: flex ;
  width: 100%;
  margin-top: 25px;
  margin-bottom: 25px;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.profile{
  padding: 35px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profile>img{
  border-radius: 50%;
  height: 120px;
  width: auto;
}
.name{
  font-size: 20px;
  color: #354364;
}
.univ-name{
  margin: 0;
  margin-top: -12px;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}
.advisor{
  margin-top: 50px;
}
.advisor>h2{
  width: 100%;
}
.advisor-h2{
  margin-left: 150px;
}

@media (max-width: 450px){
  .univ,.profile{
    margin-top: 20px;
    margin-bottom: 20px;
  }
  h1,.headings,.advisor-h2{
    text-align: center;
    margin: 0;
  }
  .menu{
    width: 100%;
    align-items: center;
  }
  .menu > li {
    margin: 10px;
  }
}